<template>
  <div
    class="swiper-video-container"
    :style="`width: ${innerWidth}; height: ${innerHeight}`"
  >
    <!-- 轮播区域 -->
    <swiper
      class="my-swipe"
      ref="playerSwiper"
      :options="swiperOptions"
      v-if="videoList.length"
      @slideChange="_slideChange"
      navigation
    >
      <swiper-slide v-for="(item, index) in videoList" :key="item.id">
        <!-- 右侧信息区域 -->
        <div class="right_menus">
          <div class="click-info" @click="item.isLike = !item.isLike">
            <div class="click-info-c">
              <img v-if="!item.isLike" src="@/assets/like.png" alt="" />
              <img v-else src="@/assets/like-act.png" alt="" />
            </div>
            <span class="click-info-text"> {{ $t('like') }} </span>
          </div>
          <div class="click-info">
            <div class="click-info-c">
              <img src="@/assets/read-r-3.png" alt="" />
            </div>
            <span class="click-info-text"> {{ $t('share') }} </span>
          </div>
          <div class="click-info">
            <div class="click-info-c">
              <img src="@/assets/download.png" alt="" />
            </div>
            <span class="click-info-text"> {{ $t('download') }} </span>
          </div>
          <div class="click-info">
            <div class="click-info-c">
              <img src="@/assets/novel-video.png" alt="" />
            </div>
            <span class="click-info-text"> {{ $t('fullFilm') }} </span>
          </div>
          <div class="toggle-swiper">
            <div class="swiper-button-prev" slot="button-prev">
              <i class="el-icon-arrow-up"></i>
            </div>
            <div class="swiper-button-next" slot="button-next">
              <i class="el-icon-arrow-down"></i>
            </div>
          </div>
        </div>
        <!-- 底部文本区域 -->
        <div class="text-container">
          <div class="nickname">
            @{{ item.nickname ? item.nickname : item.username }}
          </div>
          <div class="msg">{{ item.title }}</div>
        </div>
        <!-- 播放器 -->
        <video
          class="video-content"
          :id="`player${index}`"
          :src="item.video_url"
          preload="auto"
          controls
          loop
          width="100%"
          height="100%"
          type="video/mp4"
          playsinline="true"
          x5-playsinline="true"
          webkit-playsinline="true"
          x-webkit-airplay="allow"
          oncontextmenu="return false;"
          controlslist="nodownload noremoteplayback"
          :disablePictureInPicture="true"
        ></video>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  props: ["videoList", "innerWidth", "innerHeight"],
  data() {
    return {
      swiperOptions: {
        direction: "vertical",
        mousewheel: true,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
      },
    };
  },
  computed: {
    swiper() {
      return this.$refs.playerSwiper.$swiper;
    },
  },
  methods: {
    // swiper 切换
    _slideChange() {
      const index = this.swiper.activeIndex;
      var player = document.getElementById(`player${index}`);
      if (player.paused) {
        this.videoList.forEach((item, s_index) => {
          if (s_index == index) {
            document.getElementById(`player${s_index}`).play();
            item.play = true;
          } else {
            document.getElementById(`player${s_index}`).pause();
            item.play = false;
          }
        });
      } else {
        this.videoList.forEach((item, s_index) => {
          document.getElementById(`player${s_index}`).pause();
          item.play = false;
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.swiper-video-container {
  position: fixed;
  // width: 88%;
  // height: calc(100% - 130px);
  border-radius: 6px;
  overflow: hidden;
  cursor: pointer;
  .my-swipe {
    position: relative;
    width: 100%;
    // 解决滑动轮播时有白色线条的Bug
    height: 102%;
    .swiper-slide {
      position: relative;
      height: 100%;
      .video-content {
        width: 100%;
        height: 100%;
        background-color: #000;
        // object-fit: cover;
      }
      .right_menus {
        position: absolute;
        width: 50px;
        bottom: 80px;
        right: 20px;
        z-index: 1;
        text-align: center;
        .menuClick {
          position: relative;
          width: 100%;
          margin-bottom: 30px;
          .avatar {
            width: 42px;
            height: 42px;
            border-radius: 50%;
          }
          .follow {
            position: absolute;
            width: 20px;
            height: 20px;
            left: 50%;
            bottom: -5px;
            transform: translateX(-50%);
            font-size: 12px;
            color: #fff;
            background-color: #fe2c55;
            border-radius: 50%;
            line-height: 20px;
          }
        }
        .click-info {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-bottom: 20px;
          .click-info-c {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 30px;
            height: 30px;
            background: #f4f4f4;
            border-radius: 50%;
            margin-bottom: 4px;
            img {
              width: 28px;
              height: 28px;
            }
          }

          .click-info-text {
            font-weight: 400;
            font-size: 12px;
            color: #ffffff;
          }
        }
        .click-info:nth-child(2) {
          .click-info-c {
            img {
              width: 17px;
              height: 17px;
            }
          }
        }
        .click-info:nth-child(3) {
          .click-info-c {
            img {
              width: 17px;
              height: 17px;
            }
          }
        }
        .click-info:nth-child(4) {
          .click-info-c {
            img {
              width: 15px;
              height: 16px;
            }
          }
        }
      }
      .text-container {
        position: absolute;
        width: 30%;
        left: 0;
        bottom: 80px;
        padding: 0 30px;
        box-sizing: border-box;
        z-index: 1;
        .nickname {
          width: 100%;
          font-size: 24px;
          color: #fff;
          text-align: left;
        }
        .msg {
          position: relative;
          width: 100%;
          box-sizing: border-box;
          font-size: 18px;
          text-align: left;
          color: #fff;
        }
      }
    }
  }
  // 切换按钮
  .toggle-swiper {
    position: absolute;
    top: -120px;
    left: 50%;
    transform: translateX(-50%);
    width: 36px;
    height: 80px;
    background-color: #323442;
    border-radius: 36px;
    display: flex;
    flex-direction: column;
    opacity: 0.7;
    &:hover {
      opacity: 1;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 40px;
    position: static;
    color: #fff;
    margin-top: 0;
    &::after {
      content: "";
    }
    &:hover {
      color: #fe2c55;
    }
    i {
      font-weight: bold;
    }
  }
}
</style>
